import React from "react";
import Number from "./Number";

const Numbers = ({ persons, setPersons }) => (
  <ul>
    {persons.map(({ id, name, number }) => (
      <Number
        key={id}
        id={id}
        name={name}
        number={number}
        setPersons={setPersons}
      />
    ))}
  </ul>
);

export default Numbers;
