import React from "react";
import DeleteButton from "./DeleteButton";
import personsServices from "../services/persons";

const Number = ({ id, name, number, setPersons }) => {
  const deletePersonWrapper = (id, name) => {
    return () => {
      const answer = window.confirm(`Delete ${name} ?`);

      if (answer) {
        return personsServices.deletePerson(id).then(() => {
          setPersons((prev) => prev.filter((person) => person.id !== id));
        });
      }
    };
  };

  return (
    <li>
      {name} {number}
      <DeleteButton handleClick={deletePersonWrapper(id, name)} />
    </li>
  );
};

export default Number;
