import { logDOM } from "@testing-library/dom";
import React, { useState, useEffect } from "react";

const Filter = ({ persons, setSearchPersons }) => {
  const [searchText, setSearchText] = useState("");
  const [personsCounter, setPersonCounter] = useState(persons.length);

  console.log(personsCounter);
  const handleSearchText = ({ target }) => {
    setSearchText(target.value);
  };

  useEffect(() => {
    let searchResult = persons.filter(({ name }) => {
      const lowerCaseName = name.toLowerCase();
      const lowerCaseText = searchText.toLowerCase();

      return lowerCaseName.includes(lowerCaseText);
    });

    if (searchResult.length > 0) setSearchPersons(searchResult);
    else setSearchPersons([]);
  }, [searchText, persons]);

  return <input value={searchText} onChange={handleSearchText} />;
};

export default Filter;
