import React, { useState } from "react";
import personsServices from "../services/persons";

const Form = ({ persons, setPersons, setMessage, setSuccess }) => {
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");

  const handleNameChange = ({ target }) => setNewName(target.value);
  const handleNumberChange = ({ target }) => setNewNumber(target.value);

  const addNumber = (event) => {
    event.preventDefault();

    const personObj = {
      name: newName,
      number: newNumber,
    };

    const tmpPerson = isHere(newName);

    if (tmpPerson) {
      const anwer = window.confirm(
        `${newName} is already added to phonebook, replace the old number with a new one ?`
      );

      if (anwer) {
        personsServices
          .putPerson(tmpPerson.id, { ...tmpPerson, number: newNumber })
          .then((person) => {
            setPersons(
              persons.map((elem) => (elem.id !== tmpPerson.id ? elem : person))
            );

            setSuccess(true);
            setMessage(`Edited ${newName}`);

            setTimeout(() => setMessage(null), 5000);
          })
          .catch((error) => {
            setSuccess(false);
            setMessage(
              `Information of ${newName} has already been removed from server`
            );

            setTimeout(() => setMessage(null), 5000);
          });
      }

      setNewName("");
      setNewNumber("");
      return;
    }

    personsServices
      .postPerson(personObj)
      .then((person) => {
        setPersons([...persons, person]);
        setSuccess(true);
        setMessage(`Added ${newName}`);

        setTimeout(() => setMessage(null), 5000);
      })
      .catch((error) => {
        console.log(error);
        setSuccess(false);
        setMessage(`Name or number cant be empty`);

        setTimeout(() => setMessage(null), 5000);
      });

    setNewName("");
    setNewNumber("");
  };

  const isHere = (newName) => persons.find(({ name }) => name === newName);

  return (
    <form onSubmit={addNumber}>
      <div>
        name: <input value={newName} onChange={handleNameChange} />
      </div>
      <div>
        number: <input value={newNumber} onChange={handleNumberChange} />
      </div>
      <div>
        <button type="submit">add</button>
      </div>
    </form>
  );
};

export default Form;
