import React, { useEffect, useState } from "react";
import personsServices from "./services/persons";

import Filter from "./Components/Filter";
import Form from "./Components/Form";
import Numbers from "./Components/Numbers";
import Notification from "./Components/Notification";

const App = () => {
  const [persons, setPersons] = useState([]);
  const [searchPerson, setSearchPersons] = useState([]);
  const [message, setMessage] = useState(null);
  const [isSuccess, setSuccess] = useState(true);

  const conditionalRender = () => {
    if (searchPerson.length > 0) {
      return <Numbers persons={searchPerson} setPersons={setPersons} />;
    }

    return null;
  };

  useEffect(() => {
    console.log("Fetch data");
    personsServices.getPersons().then((initialPersons) => {
      setPersons(initialPersons);
      setSearchPersons(initialPersons);
    });
  }, []);

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={message} isSuccess={isSuccess} />
      <Filter persons={persons} setSearchPersons={setSearchPersons} />
      <h3>Add a new</h3>
      <Form
        persons={persons}
        setPersons={setPersons}
        setMessage={setMessage}
        setSuccess={setSuccess}
      />
      <h2>Numbers</h2>
      {conditionalRender()}
    </div>
  );
};

export default App;
